// src/components/Loadmore/types.ts
export interface LoadmoreProps {
  height?: number | string

  /**  */
  refresh?: () => void
  upLoadingText?: string
  upDistance?: number
  upPullText?: string
  upDropText?: string
  maxDistance?: number
  speed?: number

  /**  */
  loadMore?: () => void
  downEndText?: string
  downDropText?: string
  downLoadingText?: string
  downDistance?: number
  hasMore?: boolean

  /**  */
  styles?: Record<string, any>
  auto?: boolean
  autoFill?: boolean
}
export type LoadmoreUpStatus = 'pull' | 'drop' | 'loading' | ''
export type LoadmoreDownStatus = 'drop' | 'loading' | 'end' | ''
export const inputEmits = {
  'on-change-up-status': (value: LoadmoreUpStatus) => {},
  'on-change-down-status': (value: LoadmoreDownStatus) => {},
} as const
export type LoadmoreEmits = typeof inputEmits