.dpzvc3-badge {
  position: relative;
  display: inline-block;
  color: #ffffff;
  text-align: center;
  line-height: 1;
  vertical-align: middle;
}
.dpzvc3-badge-size-normal {
  height: 24px;
  border-radius: 24px/2;
  font-size: 15px;
  padding: 0 8px;
  min-width: 24px;
  top: -24px/2;
  line-height: 22px;
}
.dpzvc3-badge-size-large {
  height: 28px;
  border-radius: 28px/2;
  font-size: 18px;
  padding: 0 10px;
  min-width: 28px;
  top: -28px/2;
  line-height: 26px;
}
.dpzvc3-badge-size-small {
  height: 16px;
  border-radius: 16px/2;
  font-size: 12px;
  padding: 0 6px;
  min-width: 16px;
  top: -16px/2;
  line-height: 14px;
}
.dpzvc3-badge-dot {
  position: absolute;
  transform-origin: 0 center;
  top: -4px;
  right: -8px;
  height: 8px;
  width: 8px;
  min-width: 0;
  padding: 0;
  border-radius: 100%;
}
.dpzvc3-badge-success {
  background-color: #0c6;
}
.dpzvc3-badge-danger {
  background-color: #ee3555;
}
.dpzvc3-badge-warning {
  background-color: #f90;
}
.dpzvc3-badge-normal {
  background-color: #008cee;
}
.dpzvc3-badge > sup {
  position: absolute;
  right: 0;
  text-align: center;
  transform-origin: -10% center;
  z-index: 10;
  box-shadow: 0 0 0 1px #fff;
  transform: translateX(50%);
  border: 1px solid transparent;
  box-sizing: border-box;
}
.dpzvc3-card {
  background: #ffffff;
  width: 100%;
  border: 1px solid #eee;
  border-radius: 5px;
  overflow: hidden;
}
.dpzvc3-card-header,
.dpzvc3-card-footer {
  position: relative;
  box-sizing: border-box;
  padding: 0 10px;
  display: flex;
  height: 48px;
  overflow: hidden;
}
.dpzvc3-card-content {
  position: relative;
  box-sizing: border-box;
}
.dpzvc3-card-content .main {
  min-height: 300px;
}
